body {
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI"; }

.site-footer {
  padding-top: 5rem;
  background: #191919;
  min-height: 15rem;
  font-size: 1.4rem;
  color: #919191; }
  .site-footer .site-links ul {
    margin: 0;
    padding: 0; }
  .site-footer .site-links li {
    margin: 0;
    padding: 0;
    list-style: none;
    margin-bottom: 1.8rem; }
  .site-footer .site-links a {
    font-size: 1.8rem;
    color: #919191;
    font-family: 'DIN Mod'; }
  .site-footer .social-links {
    margin-bottom: 2rem; }
    .site-footer .social-links a {
      margin-left: 2rem; }
      .site-footer .social-links a i {
        display: inline-block;
        text-align: center;
        font-size: 2.5rem;
        width: 4rem;
        height: 4rem;
        line-height: 4rem;
        border-radius: 50%;
        color: #191919;
        background: #555; }

.carousel-home h1 {
  font-family: 'DIN Mod';
  text-transform: uppercase; }

.carousel-home h2 {
  font-family: 'Gotham Book';
  text-transform: uppercase; }

