@font-face {
  font-family: 'DIN Mod';
  src:  url('font-DIN-Mod.eot');
  src:  url('font-DIN-Mod.eot') format('embedded-opentype'),
        url('font-DIN-Mod.woff2') format('woff2'),
        url('font-DIN-Mod.woff') format('woff'),
        url('font-DIN-Mod.ttf') format('truetype'),
        url('font-DIN-Mod.svg#DIN-Mod') format('svg');
}

@font-face {
  font-family: 'DIN CondensedBold';
  src: url("font-DIN-Condensed-Bold.eot");
  src: url("font-DIN-Condensed-Bold.eot") format("embedded-opentype"),
       url("font-DIN-Condensed-Bold.woff") format("woff"),
       url("font-DIN-Condensed-Bold.ttf") format("truetype"),
       url("font-DIN-Condensed-Bold.svg#DIN-Condensed-Bold") format("svg");
  font-weight: normal;
  font-style: normal;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

